@import "milligram";
@import "normalize";

$facebook-blue: 	#3b5998;
$twitter-blue: 		#2caae1;
$googleplus-red: 	#da503b;
$flattr-green: 		#92BD5E;

:root {
  --highlight-color: #abcabc;
}

body{
	margin: 0
}

a {
	color: #2980b9;
}

#bar{
	background: #f4f5f6;
	border-bottom: .1rem solid #d1d1d1;
	font-size: 1.6rem;
	left: 0;
	position: fixed;
	right: 0;
	top: 0;
	width: 100%;
	z-index: 1;
	padding: 10px;

	.title{
		color: inherit;
		margin-right: 1em;

		h1{
			display: inline-block;
			font-size: 1.6rem;
			margin: 0;
			text-decoration: none;
		}
	}

	ul {
		display: inline-block;
		list-style-type: none;
		margin: 0;

		li {
			display: inline;
			margin-right: 0.5em;
			
			a{
				text-decoration: none;

				&:hover{
					text-decoration:none;
				}
			}
		}
	}
}

main {
	margin-top: 7rem;
}

#pagenav {
	display: flex; 
	text-align: center;
	margin: 5em;

	#pages {
		flex: 5 1 auto;

		.active {
			text-decoration: underline;
		}
	}
	
	#left,#right{
		flex: 0 1 auto;
	}
}

#article-list {
	article {
		margin-bottom: 4em;
	}
}

article {
	header{
		margin-bottom: 2em;

		.category{
			text-transform:uppercase;
		}

		.warning{
			padding: 1em;
			background-color: #fcf8e3;
			border-color: #faebcc;
			color: #8a6d3b;
			margin-bottom: 2em
		}

		.description {
			font-weight: bold;
		}
	}

	img{
		max-height: 20em;
		margin-left: auto;
		margin-right: auto;
	}

	figure{
		img {
			max-height: 20em;
			display: block;
			margin-left: auto;
			margin-right: auto;
		}

		figcaption {
			text-align: center;
		}
	}

	footer{
		#share{
			margin: 3em 0 3em 0;

			.share-icon{
				color: white;
				padding: 5px;
			}

			.share-icon:hover{
				text-decoration: none;
				color: #eee;
			}

			.facebook{
				background-color: $facebook-blue;
			}

			.twitter{
				background-color: $twitter-blue;
			}

			.googleplus{
				background-color: $googleplus-red;
			}

			.flattr{
				background-color: $flattr-green;
			}
		}

		#comments {
			margin-top:5em
		}
	}
}
